import React, { useState } from 'react';
import { List, InputItem } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import { Page, PageTitle } from '../../Login/styles';

const { Item } = List;

const Safe: React.FC = () => {
  const history = useHistory();
  const handleChangePwd = (): void => {
    history.push('/change-pwd');
  };
  return (
    <Page>
      <PageTitle>账号与安全</PageTitle>
      <List>
        <InputItem disabled value="abcwangzhan@163.com">
          邮箱：
        </InputItem>
        <InputItem disabled value="wangzhan">
          用户名：
        </InputItem>
        <Item arrow="horizontal" onClick={handleChangePwd}>
          更改密码
        </Item>
      </List>
    </Page>
  );
};

export default Safe;
